:root {
  --mb-2: 1rem;
  --mb-4: 1.5rem;
  --font-semi: 600;
  --first-color: #4caf50;
  --z-back: -1;
}

.skills {
  padding: 2rem 0;
}

.section-title {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 1rem;
  color: #333;
}

.skills__container {
  display: grid;
  grid-row-gap: 2rem;
  row-gap: 2rem;
  text-align: center;
  grid-template-columns: 1fr;
}

.skills__frontend,
.skills__backend,
.skills__others {
  margin: 1rem;
  padding: 1rem;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background-color: #f9f9f9;
  width: 300px;
  height: 450px;
}

.skills__subtitle {
  font-size: 1.5rem;
  margin-bottom: var(--mb-2);
  color: #555;
}

.skills__text {
  margin-bottom: var(--mb-4);
  color: #777;
  line-height: 1.6;
}

.skills__data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: var(--font-semi);
  padding: 0.5rem 1rem;
  margin-bottom: var(--mb-4);
  border-radius: 0.5rem;
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
}

.skills__icon {
  font-size: 2rem;
  margin-right: var(--mb-2);
  color: var(--first-color);
}

.skills__names {
  display: flex;
  align-items: center;
}

.skills__bar-container {
  flex: 1 1;
  height: 25px;
  background: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  margin-left: 1rem;
}

.skills__percentage {
  height: 100%;
  background-color: var(--first-color);
  color: #fff;
  text-align: right;
  padding: 0 5px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  border-radius: 5px;
}

.skills__container {
  display: grid;
  grid-template-columns: 1fr; /* Chỉ có một cột */
  justify-items: center; /* Căn giữa theo chiều ngang */
  align-items: center; /* Căn giữa theo chiều dọc */
}
/*=== Media Queries ===*/
@media screen and (min-width: 768px) {
  .skills__container {
    grid-template-columns: repeat(2, 1fr);
    justify-items: stretch;
    align-items: center;
  }
}
@media screen and (min-width: 1024px) {
  .skills__container {
    grid-template-columns: repeat(3, 1fr);
  }
}

.work {
  text-align: center;
}
.work__container {
  display: grid;
  grid-row-gap: 10rem;
  row-gap: 10rem;
  justify-items: center;
}
.work__img {
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
  border-radius: 2rem;
  overflow: hidden;
  object-fit: "cover";
  /* height: 150%;
  width: 50%; */
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Căn giữa các phần tử con */
  text-align: center;
}
.size-img img {
  object-fit: cover;
  border-radius: 2rem;
  height: 490px;
  width: 240px;
}
.size-img img:hover {
  transition: 1s;
  cursor: pointer;
  transform: scale(1.1);
}
.work__img img {
  transition: 1s;
  cursor: pointer;
  object-fit: "cover";
  /* height: 150%;
  width: 50%; */
}
.work__img img:hover {
  transform: scale(1.1);
}
/*=== Media Queries ===*/
@media screen and (min-width: 768px) {
  .work__container {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 2rem;
    column-gap: 2rem;
  }
}
.title {
  font-size: var(--h6-font-size);
  color: var(--first-color);
  text-align: left; /* Căn trái */
  margin-left: 20px;
}
.circle {
  width: 50px; /* Đường kính của hình tròn */
  height: 50px; /* Đường kính của hình tròn */
  border-radius: 50%; /* Làm cho phần tử thành hình tròn */
  background-color: var(--first-color); /* Màu nền của hình tròn */
  color: white; /* Màu chữ */
  display: flex;
  align-items: center; /* Căn giữa theo chiều dọc */
  justify-content: center; /* Căn giữa theo chiều ngang */
  font-size: 24px; /* Kích thước chữ */
  font-weight: bold; /* Đậm chữ */
  margin: 16px;
}
.row {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* Căn trái các mục trong .row */
  margin-bottom: 1rem;
  width: 100%; /* Căn giữa theo chiều dọc */
}

